<template>
    <div>
    
        <div class="wrapper">
            <app-sidebar></app-sidebar>
            <div class="main-panel">
                <!-- fixed plugin  -->
                <!-- Navbar -->
                <app-nav></app-nav>
        
                <!-- End Navbar -->
            <div class="content">
                <div class="container-fluid">
                    <div class="d-flex flex-wrap justify-content-between head_loc">
                        <div class="col-md-6 p-0">
                            <p class="m-0 dir_title">Services</p>
                            <p class="m-0 dir_point">
                                <span>Home -</span>
                                <span> Sonocare -</span>
                                <span class="p_point"> Services -</span>
                                <span class="p_point"> Sub-services - </span>
                                <span class="p_point"> Children</span>
                            </p>
                        </div>
                        <div class="col-md-6 p-0 dash_sear">
                                <div class="input-group sear">
                                    <div class="input-group-prepend">
                                        <span class="input-group-text" id="basic-addon1"><i class="fa fa-search"></i></span>
                                    </div>
                                    <input type="text" class="form-control" placeholder="Service" aria-label="Service" v-model="search_child">
                                </div>
                        </div>
                    </div>
                    <div class="row">
                        <div class="container-fluid">
                            <div class="add_service">
                                <button class="service_btn" @click="addNewChildForm()">Add New Child Service</button>
                            </div>
                            <div class="new_service_detail" v-if="add_new_child">
                                <div class="new_service mt-3">
                                    <div class="col-md-6 p-0">
                                        <input type="text" class="form-control" v-model="new_child_name" placeholder="child Name"><br>
                                        <textarea type="text" class="form-control" v-model="new_child_desc" placeholder="child Description"></textarea><br>
                                        <input type="number" class="form-control" v-model="new_child_amount" placeholder="child Amount"><br>
                                        <div class="form-group ml-1">
                                    <div class="col-md-12 d-flex justify-content-between flex-wrap">
                                        <input class="form-check-input" type="checkbox" id="gridCheck" v-model:checked="new_child_homeService">
                                        <label class="form-check-label" for="gridCheck">
                                            Home Service
                                        </label>
                                    </div>
                                </div>
                                        <button class="btn-block add_service_btn" @click="addNewChild()">Add Child Service</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="row">
                        <div class="container-fluid service_p">
                            <div class="row">
                                <div class="col-md-4 service_gp" v-for="(child,index) in filterService">
                                    <div class="service_c text-center">

                                        <img v-bind:src="child.picture" class="rounded-circle p_img" width="100px"><br>
                                        <div class="form-group">
                                             <label v-bind:for="'image_edit'+index"><i class="fa fa-edit"></i>Edit</label>
                                             <input type="file" ref="file" class="form-control-file" v-bind:id="'image_edit'+index" style="display:none" v-on:change="handleFileUpload(child,$event)">
                                        </div> 
                                        <template v-if="!child.edit_service">
                                            <span>{{child.name}}</span><br>
                                            <button class="edit_service" @click="editChild(child)">Edit</button>
                                        <button class="cancel_edit_service" @click="deleteChild(index,child)">Delete child</button>
                                        </template>

                                        <template v-else>
                                            <br>
                                            <input type="text" class="form-control" v-model="child.name">
                                            <textarea type="text" class="form-control" v-model="child.description"></textarea>
                                            <input type="text" class="form-control" v-model="child.amount">
                                            <div class="col-md-12 d-flex justify-content-between flex-wrap">
                                                  <input class="form-check-input" type="checkbox" id="gridCheck" v-model:checked="child.homeService">
                                                   <label class="form-check-label" for="gridCheck">
                                                  Home Service
                                                  </label>
                                            </div>
                                            <button class="edit_service" @click="saveEditChild(child)">Save</button>
                                            <button class="cancel_edit_service" @click="cancelEditChild(child)">cancel</button>
                                        </template>
                                    </div>
                                </div>
                                
                            </div>
                        </div>
                    </div>
                </div>
            </div>
    </div>
            <loader-gif v-if="loader"></loader-gif>
    </div>
    </div>
</template>

<script>
    const axios = require('axios');
    const qs = require('query-string');
   export default {
  name: 'panel',
  data () {
    return { 
        subservice_id:this.$route.params.subservice,
        childs:[],
        token:'',
        search_child:'',
        add_new_child:false,
        new_child_name:'',
        new_child_desc:'',
        new_child_amount:'',
        new_child_homeService:false,
        children:[],
        loader : false,
        
    }
  },
  computed:{
            filterService(){
                return this.children.filter((child)=>{
                    return child.name.toLowerCase().includes(this.search_child.toLowerCase())
                })
            },
            
},
    methods: {
        handleFileUpload(child,event){
               var _id = child._id;
               console.log(_id);
                this.file = event.target.files[0];
                console.log(this.file);

                let formData = new FormData();

                    formData.append('image', this.file);
                    this.loader = true;
                    axios.post(this.$endpoint+'/api/v1/sub-service/image/'+_id,
                        formData,
                        {
                        headers: {
                            'Content-Type': 'multipart/form-data',
                            'x-access-token' : this.token
                        }
                        }
                    ).then((resp)=>{
                        this.loader = false;
                        console.log(resp);
                        // this.$session.set('image',resp.data.data.picture);
                        child.picture = resp.data.data.picture
                })
                .catch((err)=>{
                    this.loader = false;
                     console.log(err);
                });
                console.log(this.token);
                        console.log(child.picture);
      },
        child(child){
            var _id = child._id;
            this.$router.push('/subchild/'+_id);
        },
      editChild(child){
          child.edit_service = true;
      },
      cancelEditChild(child){
          child.edit_service = false;
          child.name = child.temp_name;
          child.description = child.temp_desc;
          child.amount = child.temp_amount;

      },
      addNewChildForm(){
          this.add_new_child = !this.add_new_child;
      },
      saveEditChild(child){
          this.loader = true;
          var _id = child._id;
          axios.put(this.$endpoint+"/api/v1/sub-service/"+_id,
            {
                "name" : child.name,
                "description" : child.description,
                "amount" : child.amount,
                "homeService" : child.homeService
            },
            {
                headers:{
                    "x-access-token":this.token
                }
            })
            .then((resp)=>{
                this.loader = false;
                child.edit_service = false;
                console.log(resp);
            })
            .catch((err)=>{
                this.loader = false;
                console.log(err.response);
            })
      },
      addNewChild(){
          var _id = this.subservice_id;
          console.log(this.new_child_homeService);
          const data = qs.stringify({  
                        "name" : this.new_child_name,
                        "description" : this.new_child_desc,
                        "amount" : this.new_child_amount,
                        "home_service" : this.new_child_homeService
                    });
          if(this.new_child_name != '' && this.new_child_desc != ''){
              this.loader = true;
                axios.post(this.$endpoint+"/api/v1/sub-service/"+_id,
                    data
                    ,
                    {
                        headers:{
                            "Content-Type":"application/x-www-form-urlencoded",
                            "x-access-token":this.token
                        }
                    }
                )
                .then((resp)=>{
                    this.loader = false;
                    this.children.unshift({
                        picture:'https://cdn1.iconfinder.com/data/icons/inficons-set-3-1/1000/chemistry-flasks-2-512.png',
                        name: this.new_child_name,
                        description: this.new_child_desc,
                        amount : this.new_child_amount,
                        homeService: this.new_child_homeService,
                        edit_service : false,
                        temp_name : this.new_child_name,
                        temp_desc : this.new_child_desc,
                        temp_amount : this.new_child_amount,
                    });
                    this.addNewChildForm();
                    console.log('resp',resp);
                })
                .catch((err)=>{
                    this.loader = false;
                     console.log(err)
                })
            }
      },
      
      deleteChild(index,child){
          var _id = child._id;
          console.log(_id);
          var ret = confirm('Are you sure');
          if(ret == true){
              this.loader = true;
            axios.delete(this.$endpoint+"/api/v1/sub-service/"+_id,{
                headers:{
                    "Content-Type": "application/text/plain",
                    "x-access-token":this.token
                }
            })
            .then((resp)=>{
                this.loader = false;
                this.children.splice(index,1);
            })
            .catch((err)=>{
                this.loader = false;
                console.log(err.response);
            })
          }

      },

      logout: function () {
        this.$session.destroy()
        this.$router.push('/login')
        }
    
    },
    mounted(){
        this.loader = true;
        // alert(this.service_id);
        this.token = this.$session.get('jwt');
        console.log(this.token);
        axios.get(this.$endpoint+"/api/v1/sub-service/"+this.subservice_id)
        .then((resp)=>{
            this.loader = false;
            for(var i of resp.data.data){
                if(i.picture == null){
                    i.picture = 'https://cdn1.iconfinder.com/data/icons/inficons-set-3-1/1000/chemistry-flasks-2-512.png';
                }
                this.children.push({
                    "_id": i._id,
                    "picture":i.picture,
                    "name":i.name,
                    "description":i.description,
                    "amount":i.amount,
                    "homeService":i.homeService,
                    "edit_service" : false,
                    "temp_name" : i.name,
                    "temp_desc" : i.description,
                });
                }
                console.log('resp',resp.data.data);
        })
        .catch((err)=>{
            this.loader = false;
            console.log(err)
        })
  }
}
</script>

