<template>
    <div class="main">
        <div class="main_c">
            <div class="head_img">
                <img src="../assets/logo.png">
            </div>
            <div class="todo_bdy">
                <div>
                    <input v-model="newTodo" v-on:keyup.enter="addTodo" type="text" placeholder="What do you want to do?">
                </div>
                <div>
                    <p>Todo list goes here</p>
                    <div v-for="(todo,index) in todos" v-bind:key="todo.id">
                       <div class="todos">
                       <input type="checkbox" v-model="todo.completed">
                       <div class="todo-item-label" v-bind:class="{completed : todo.completed}" v-if="!todo.editting" v-on:dblclick="editTodo(todo)">
                            {{todo.title}}
                       </div>
                       <div v-else class="todo-item-edit-p">
                            <input class="todo-item-edit" type="text" v-model="todo.title" @blur="doneEditting(todo)" @keyup.enter="doneEditting(todo)" v-focus @keyup.esc="cancelEdit(todo)"> 
                       </div>
                       </div>
                       <div class="remove_item" v-on:click="removeTodo(index)">
                            &times
                       </div>
                    </div>
                </div>
            </div>

        </div>
        <div class="extra_c">
            <div><label><input type="checkbox">Check All</label></div>
            <div>{{remaining}} items left</div>
        </div>
    </div>  
</template>

<script>
    export default{
        data(){
            return{
                namer:"namers",
                idForTodo:3,
                tempText:'',
                newTodo: '',
                todos:[
                    {
                        'id':1,
                        'title':'Finish this work quick',
                        'completed':false,
                        'editting':false,
                    },
                    {
                        'id':2,
                        'title':'Take over the world',
                        'completed':false,
                        'editting':false,
                    }
                ],            
            }
        },
        directives:{
            focus:{
                inserted:function(el){
                    el.focus();
                }
            }
        },
        methods:{
            addTodo(){
                if(this.newTodo.trim() == 0){
                    return;
                }
                this.todos.push({
                    'id':this.idForTodo,
                    'title':this.newTodo,
                    'completed':false
                });
                this.newTodo = '';
                this.idForTodo++;
            },
            removeTodo(index){
                this.todos.splice(index,1);
            },
            editTodo(todo){
                todo.editting = true;
                this.tempText = todo.title;
            },
            doneEditting(todo){
                if(todo.title.trim() == ''){
                    todo.title = this.tempText;
                }
                todo.editting = false
            },
            cancelEdit(todo){
                todo.title = this.tempText;
                todo.editting = false
            }
        },
        computed:{
            remaining(){
                return this.todos.filter(todo => !todo.completed).length
            }
        }
    }
</script>



<style scoped>
    .main{
        width:100%;
        margin:auto;
        padding-top:30px;
        text-align:center
    }
    .head_img img{
        width:50px;
    }
    .head_img{
        text-align:center;
    }
    .remove_item{
        display:inline;
    }
    .todos, .todos div{
        display:inline;
    }
    .completed{
        text-decoration:line-through;
        color:grey;
    }

</style>