<template>
    <div>
    <div class="wrapper">
            <pa-app-sidebar></pa-app-sidebar>
            <div class="main-panel">
                <!-- Navbar -->
                <pa-app-nav></pa-app-nav>
            <div class="content">
                <div class="container-fluid">
                <div class="d-flex flex-wrap justify-content-between head_loc">
                        <div class="col-md-6 p-0">
                            <p class="m-0 dir_title">Appointment</p>
                            <p class="m-0 dir_point">
                                <span>Home -</span>
                                <span>Sonocare -</span>
                                <span>Partner -</span>
                                <span class="p_point">Appointment</span>
                            </p>
                        </div>
                        <div class="col-md-6 p-0 dash_sear">
                            <div class="input-group sear">
                                <div class="input-group-prepend">
                                    <span class="input-group-text" id="basic-addon1"><i class="fa fa-search"></i></span>
                                </div>
                                <input type="text" class="form-control" placeholder="Appointment" aria-label="Appointment" aria-describedby="basic-addon1">
                            </div>
                            
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-12">
                            <template v-if="appointStatus == status[0]">
                            <div>
                                <button class="act_btn" @click="acceptApp()">Accept</button>
                                <!--<button class="act_btn" @click="reschApp()">Rescheduled</button>-->
                                <button class="act_btn" @click="declineApp()">Decline</button>
                            </div>
                            </template>
                            <template v-else-if="appointStatus == status[1] && !reportStatus">
                            <div>
                                <button class="act_btn" @click="showCrtBtn()">Create Report</button>
                                <button class="act_btn">Delete</button><br>
                                
                            </div>
                            </template>
                            <template v-if="crtBtn">
                                    <div>
                                        <input type="text" placeholder="Report title" class="reportTitle form-control" v-model="title"><br>
                                        <textarea placeholder="Report description" class="reportDesc form-control" v-model="description"></textarea><br>
                                        <button @click="createReport()" class="crtBtn">Create Report</button>
                                    </div>
                            </template>
                            <template v-if="reportStatus">
                                <div>
                                    <label for="fileUp" class="rptfBtn">Upload File</label><br>
                                    <input type="file" ref="file" class="form-control-file" id="fileUp" style="display:none" v-on:change="handleFileUpload()">
                                </div>
                            </template>
                            <template v-if="reportFile == ''">
                                <div>
                                    <p>No file yet</p>
                                </div>
                            </template>

                            <div class="row" v-if="reportFile != ''">
                        <div class="col-md-12">
                            <div class="card striped-tabled-with-hover">
                                <div class="card-body table-responsive">
                                    <table class="table table-hover table-striped">
                                        <thead class="h_head">
                                            <th>File</th>
                                            <th>Date, Time</th>
                                            <th>Download</th>
                                            <th>Delete</th>
                                        </thead>
                                        <tbody class="h_body">
                                            <tr v-for="file, index in reportFile">
                                                <td>{{file.fieldname}}</td>
                                                <td>{{dateChanger(file.createdAt)}}</td>
                                                <td><a v-bind:href="file.url" download target="_blank"><button class="col-12 downBtn">Download<i class="fa fa-download"></i></button></a></td>
                                                <td><button @click="delFile(file._id, index)" class="delBtn form-control">Delete<i class="fa fa-times"></i></button></td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                            
                        </div>
                    </div>
                        </div>
                    </div>
                </div>
            </div>
            </div>
            <loader-gif v-if="loader"></loader-gif>
    </div>
    </div>
</template>

<script>
    const axios = require('axios');
    export default{
        data(){
            return{

                crtBtn : false,
                app_id : this.$route.params._id,
                token:'',
                patientDetail : [],
                appointStatus : '',
                reportStatus : false,
                report : [],
                reportFile : [],
                status : ['patient-initiated','partner-accepted'],
                report_id : '',
                fileUp : '',
                title: '',
                description : '',
                loader : false,
            }
        },
        methods:{
            showCrtBtn(){
                this.crtBtn = !this.crtBtn;
            },
            delFile(_id,index){
                this.loader = true;
                axios.delete(this.$endpoint+'/api/v1/report/'+this.report_id+'/file/'+_id,{
                    headers:{
                        'Content-Type': 'multipart/form-data',
                        "x-access-token":this.token
                    }
                })
                .then((resp)=>{
                    this.loader = false;
                    alert('file removed succefully');
                    this.reportFile.splice(index,1);
                })
                .catch((err)=>{
                    this.loader = false;
                    alert(err.response.data.message);
                    console.log(err);
                })
            },
            handleFileUpload(){
                this.fileUp = this.$refs.file.files[0];
                console.log(this.fileUp);

                let formData = new FormData();

                    formData.append('file', this.fileUp);
                    this.loader = true;
                    axios.post(this.$endpoint+'/api/v1/report/'+this.report_id+'/file',
                        formData,
                        {
                        headers: {
                            'Content-Type': 'multipart/form-data',
                            'x-access-token' : this.token
                        }
                        }
                    ).then((resp)=>{
                        this.loader = false;
                        alert ('file uploaded');
                        console.log(resp);
                        this.reportFile.push(resp.data.data);
                      })
                    .catch((resp)=>{
                        this.loader = false;
                        alert(err.response.data.message);
                          console.log(resp);
                    });
      },
            acceptApp(){
                this.loader = true;
                axios.post(this.$endpoint+'/api/v1/appointment/partner/accept/'+this.app_id,{},{
                    headers:{
                        "x-access-token":this.token
                    }
                })
                .then((resp)=>{
                    this.loader = false;
                    alert('Appointment Accepted');
                })
                .catch((err)=>{
                    this.loader = false;
                    // alert(err.response.data.message);
                    console.log(err.message);
                    alert('Error in accepting Appointment');
                });
            },
            createReport(){
                var title = this.title;
                var desc = this.description;
                if(title != '' && desc != ''){
                    this.loader = true;
                    axios.post(this.$endpoint+'/api/v1/report/appointment/'+this.app_id,{
                        "title":title,
                        "description":desc
                    },{
                        headers:{
                            "x-access-token":this.token
                        }
                    })
                    .then((resp)=>{
                        this.loader = false;
                        this.reportStatus = true;
                        alert('Report Created');
                    })
                    .catch((err)=>{
                        this.loader = false;
                        alert(err.message);
                        console.log(err.message);
                        alert('Error in creating Report');
                    });
                }else{
                    alert('Please complete the detail');
                }
            },
            declineApp(){
                this.loader = true;
                axios.post(this.$endpoint+'/api/v1/appointment/partner/decline/'+this.app_id,{},{
                    headers:{
                        "x-access-token":this.token
                    }
                })
                .then((resp)=>{
                    this.loader = false;
                    alert('Appointment Declined');
                })
                .catch((err)=>{
                    this.loader = false;
                    alert(err.response.data.message);
                    console.log(err);
                    alert('Error in declining Appointment');
                })
            },
            dateChanger(date){
                let dater = new Date(date);
                    dater = dater.toString();
                    console.log(dater);
                    var pos = dater.indexOf("G");
                    dater = dater.slice(0,pos);
                    return dater;
            }
        },
        mounted(){
            this.loader = true;
            this.token = this.$session.get('jwt');
            console.log(this.token);
            console.log(this.app_id);
            axios.get(this.$endpoint+'/api/v1/appointment/partner/'+this.app_id,{
                headers:{
                    "x-access-token":this.token
                }
            })
            .then((resp)=>{
                this.loader = false;
                var data = resp.data.data;
                this.patientDetail = data.patient;
                this.appointStatus = data.status;
                console.log(this.appointStatus);
                console.log(resp);
            })
            .then((resp)=>{
                this.loader = true;
                axios.get(this.$endpoint+'/api/v1/report/appointment/'+this.app_id,{
                    headers:{
                        "x-access-token":this.token
                    }
                })
                .then((resp)=>{
                    this.loader = false;
                    var data = resp.data.data
                    if(data == null){
                        this.reportStatus = false;
                    }else{
                        this.reportStatus = false;
                        this.reportStatus = true;
                        this.report = data;
                        this.reportFile = data.files;
                        this.report_id = data._id;
                        console.log(this.reportFile);
                        console.log('report =>',this.report_id);
                    }
                })

            })
            .catch((err)=>{
                alert(err.message);
                alert(err.response.data.message);
                this.loader = false;
                console.log(err);
            })
        }
        
    }

</script>
<style scoped>
    .act_btn, .crtBtn, .rptfBtn{
        border:none;
        border-radius:5px;
        background:#0078C8;
        margin:0px 10px 10px 0px;
        padding:20px;
        color:white;
    }
    .crtBtn{
        padding:10px;
        background:#F67D00;
    }
    .rptfBtn{
        padding:10px;
        background:#F67D00;
    }
    .downBtn{
        background:#00DAA1;
        color:#fff;
    }
    .delBtn{
        background:#F23D31;
        color:#fff;
    }

</style>