<script>
    const axios = require('axios');
    import { GChart } from 'vue-google-charts';
   export default {
       name: 'panel',
       components: {
             GChart
        },
  data () {
    return { 
        chartData: [
        ['Year', 'Partners'],
      ],
      chartOptions: {
        chart: {
          title: 'Partner',
          subtitle: 'Partner',
        }
      },
        token : '',
        app_count : 0,
        partner_count : 0,
        patient_count : 0,
        upApp : [],
        loader : false,
        shower : false,

    }
  },
  methods: {
    logout: function () {
      this.$session.destroy()
      this.$router.push('/login')
    },
    dateChanger(date){
                let dater = date;
                    var pos = dater.indexOf(".");
                    dater = dater.slice(0,pos);
                    dater = dater.replace('T',' ');
                    pos = dater.indexOf(" ");
                    dater = dater.slice(0,pos);
                    return dater;
            },
    timeChanger(date){
        let dater = date;
                    var pos = dater.indexOf(".");
                    dater = dater.slice(0,pos);
                    dater = dater.replace('T',' ');
                    pos = dater.indexOf(" ");
                    dater = dater.slice(pos);
                    return dater;
    },
    removeApp(index){
        this.upApp.splice(index,1);
    },
    seeApp(index){
        this.upApp.splice(index,1);
    },

  },
  mounted(){
      this.loader = true;
      this.token = this.$session.get('jwt');
        axios.get(this.$endpoint+'/api/v1/appointment/admin',{
            headers:{
                "x-access-token":this.token
            }
        })
        .then((resp)=> {
            this.loader = false;
            var data = resp.data.data;
            this.app_count = data.length;
            for(let i of data){
                if(i.status != "partner-accepted"){

                    this.upApp.push(i);
                }
            }
            console.log(this.upApp);
        })
        .catch((err)=>{
            this.loader = false;
            console.log(err.response.data.message);
        })

        axios.get(this.$endpoint+'/api/v1/patient?limit=200',
            {
                headers : {
                    "x-access-token" : this.token
                }
            }
        )
        .then((resp)=> {
            this.patient_count = resp.data.data.length;
        })
        .catch((err)=>{
            console.log(err.response.data.message);
        })

        axios.get(this.$endpoint+'/api/v1/partner?limit=200')
        .then((resp)=> {
            this.partner_count = resp.data.data.length;
        })
        .catch((err)=>{
            console.log(err.data.response.message);
        });

        axios.get('http://sonocareapp.eastus.cloudapp.azure.com/api/v1/stats/graph/monthly')
        .then((resp)=>{
            let data = resp.data.data;
            console.log(data);
            let dat;
            for(let i in data){
                dat = [i.substring(0,3), data[i]];
                this.chartData.push(dat);
            }
        })
        .catch((err)=>{
            console.log(err);
        })

  }
}
    
</script>

