<script>
    const axios = require('axios');
    export default{
        data(){
            return {
                data : '',
                profile_img : '',
                name : '',
                email : '',
                phone : '',
                address : '',
                sex : '',
                dob : '',
                token : '',
                loader : false,
            }
        },
         methods:{
            handleFileUpload(){
                this.file = this.$refs.file.files[0];
                console.log(this.profile_img);
                console.log(this.file);

                let formData = new FormData();
                    this.loader = true;
                    formData.append('image', this.file);

                    axios.post(this.$endpoint+'/api/v1/profile/picture',
                        formData,
                        {
                        headers: {
                            'Content-Type': 'multipart/form-data',
                            'x-access-token' : this.token
                        }
                        }
                    ).then((resp)=>{
                        this.loader = false;
                        this.$session.set('image',resp.data.data.image_link);
                        this.profile_img = resp.data.data.image_link
                })
                .catch(function(resp){
                    this.loader = false;
                     console.log(resp);
                });
                console.log(this.token);
                        console.log(this.profile_img);
      }
        },
        mounted(){
            // alert('l');
                this.loader = true;
            this.data = this.$session.get('data');
            if(this.data.type == 'patient'){
                this.loader = false;
                console.log(this.data)
                if(this.data.profile_picture == undefined){
                    if(this.$session.get('image')){
                        this.profile_img = this.$session.get('image');
                    }else{
                        this.profile_img = 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcR05-WvgvoVVFR0fQCTWhyhiTHGinaX4FK8-Dux8QrLzrSZ8oQ3SQ';
                    }
                }else{
                    if(this.$session.get('image')){
                        this.profile_img = this.$session.get('image');
                    }else{
                        this.profile_img = this.data.profile_picture;
                    }
                };
                this.name = this.data.first_name + ' ' + this.data.last_name;
                this.email = this.data.email;
                this.phone = this.data.phone;
                this.address = this.data.address;
                this.address = this.data.address;
                this.sex = this.data.sex;
                this.dob = this.data.date_of_birth;
                this.token = this.$session.get('jwt');
            }else{
                this.$router.push('/login');
            }
        }

    }
</script>

