<script>
const axios = require('axios');
    export default{
        data(){
            return{
                token : '',
                searchUser:'',
                name: 'Toluwap',
                patients : [],
                cust : [],
                loader : true
            }
        },
        methods:{
            editCustomer(customer){
                customer.editting = true;
            },
            saveEditting(customer){
                // axios.push("")
                customer.editting = false;
            },
            removeCustomer(index){
                this.cust.splice(index,1);
            },
            activator(customer){
                var _id = customer._id;
                this.loader = true;
                this.token = this.$session.get('jwt');
                axios.post(this.$endpoint+"/api/v1/admin/partner/activate/"+_id,{},
                {
                    headers : {
                        "x-access-token" : this.token
                    }
                }
                )
                .then((resp)=>{
                    customer.active = true;
                    this.loader = false;
                    console.log("Activated");
                })
                .catch((err)=>{
                    this.loader = false;
                    alert("Error activating partner");
                })
            },
            deactivator(customer){
                var _id = customer._id;
                this.loader = true;
                this.token = this.$session.get('jwt');
                console.log(this.token);
                axios.post(this.$endpoint+"/api/v1/admin/partner/deactivate/"+_id,{},
                {
                    headers : {
                        "x-access-token" : this.token,
                    }
                }
                )
                .then((resp)=>{
                    customer.active = false;
                    this.loader = false;
                    console.log("Deactivated");
                })
                .catch((err)=>{
                    this.loader = false;
                    alert("Error deactivating partner");
                })
            },
        },
        computed:{
            filterUser(){
                return this.cust.filter((customer)=>{
                    return customer.name.toLowerCase().includes(this.searchUser.toLowerCase())
                })
            },
            
        },
        mounted(){
            this.loader = true;
            this.token = this.$session.get('jwt');
            axios.get(this.$endpoint+"/api/v1/partner?limit=200",
            {
                headers : {
                    "x-access-token" : this.token
                }
            }
            )
            .then((resp)=>{
                    this.loader = false;
                    this.partners = resp.data.data;
                    // console.log(this.partners);
                    for(var partner of this.partners){
                        if(partner.profile_picture == undefined){
                            console.log('img not defined');
                            partner.profile_picture = 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcR05-WvgvoVVFR0fQCTWhyhiTHGinaX4FK8-Dux8QrLzrSZ8oQ3SQ';
                        }
                        this.cust.push(
                            {
                                '_id':partner._id,
                                'active': partner.active,
                                'img': partner.profile_picture,
                                'name': partner.institution_name,
                                'country': 'Nigeria',
                                'mobile': partner.phone,
                                'address': partner.institution_address,
                                'email': partner.email,
                                'type':partner.institution_type,
                                'editting':false
                            }
                        )
                     };
                     console.log(this.cust);
                    //  console.log(this.customers);
                }
            )
            .catch((err)=>{
                this.loader = false;
                console.log(err)
                });

            
        }
    }

</script>

