<template>
    <div>
    <div class="wrapper">
            <p-app-sidebar></p-app-sidebar>
            <div class="main-panel">
                <!-- Navbar -->
                <p-app-nav></p-app-nav>
            <div class="content">
                <div class="container-fluid">
                    <div class="d-flex flex-wrap justify-content-between head_loc">
                        <div class="col-md-6 p-0">
                            <p class="m-0 dir_title">{{type}}</p>
                            <p class="m-0 dir_point">
                                <span>Home -</span>
                                <span>Sonocare -</span>
                                <span class="p_point">{{type}}</span>
                            </p>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-12">
                            <div class="container-fluid card p-3">
                                <div class="d-flex justify-content-center">
                                    <img src="../../assets/img/ultra_main.png">
                                </div>
                                <div class="row">
                                    <div class="col-12">
                                        <div class="container-fluid">
                                            <p>Select your location</p>
                                        </div>

                                        <div class="d-flex flex-wrap">
                                            <div class="col-md-6">
                                                <vue-google-autocomplete
                                                    ref="address"
                                                    id="map"
                                                    classname="form-control"
                                                    placeholder="Please type your address"
                                                    v-on:placechanged="getAddressData"
                                                    v-model = "location"
                                                >
                                                </vue-google-autocomplete>
                                            </div>
                                            <div class="col-md-6">
                                                <select class="custom-select custom-select-lg mb-3 col-md-12" v-model="partner_id_opt">
                                                    <option selected>Nearest Hospitals / Offices</option>
                                                    <option v-for="partner in partners" v-bind:value="partner._id">{{partner.institution_name}} {{partner.institution_address}}</option>
                                                </select>
                                            </div>
                                        </div>
                                        <div class="d-flex flex-wrap mt-4">
                                            <div class="col-md-6 text-center">
                                                <div class="form-check">
                                                    <input class="" type="radio" name="exampleRadios" id="exampleRadios1" value="option1" checked>
                                                    <label class="form-check-label" for="exampleRadios1">
                                                        Home Service
                                                    </label>
                                                </div>
                                            </div>
                                            <div class="col-md-6 text-center">
                                                <div class="form-check">
                                                    <input class="" type="radio" name="exampleRadios" id="exampleRadios1" value="option1" checked>
                                                    <label class="form-check-label" for="exampleRadios1">
                                                        Appointment
                                                    </label>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="col-md-12 mt-3 mb-3">
                                        <p>Appointment Date</p>
                                            <datetime type="datetime" v-model="app_date" class="dater" placeholder="pick appointment date"></datetime>
                                        </div>
                                        <div class="d-flex">
                                            <div class="container-fluid">
                                                <p>{{type}} type</p>
                                            </div>
                                        </div>
                                        <div class="col-md-12 mb-3">
                                            <select class="custom-select custom-select-lg mb-3 col-md-12" v-model="subservice_id_opt">
                                                    <option selected>Select Your Labtest type</option>
                                                    <option v-for="service in sub_service" v-bind:value="service._id">{{service.name}}</option>
                                                </select>
                                        </div>
                                    </div>

                                </div>
                            </div>
                            <div class="col-md-12">
                                <button class="btn_p btn btn-block" @click="requst_app">Request Now</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            </div>
            <loader-gif v-if="loader"></loader-gif>
    </div>
    </div>
</template>


<script>
    import VueGoogleAutocomplete from 'vue-google-autocomplete'
    const axios = require('axios');
    import { Datetime } from 'vue-datetime';
    import 'vue-datetime/dist/vue-datetime.css'
    export default {
        components: { VueGoogleAutocomplete,datetime: Datetime  },
 
        data: function () {
            return {
                type:'LabTest',
              partner_id_opt : 'Nearest Hospitals / Offices',
              subservice_id_opt : 'Select Your Labtest type',
              app_date : '',
              service_name : 'Lab Test',
              service_id : '',
              address : '',
              location : '',
              partners:[],
              sub_service : [],
              token : '',
              loader : false,
            }
        },
 
        mounted() {
            this.loader = true;
            this.token = this.$session.get('jwt');
            // To demonstrate functionality of exposed component functions
            // Here we make focus on the user input
            this.$refs.address.focus();

            axios.get(this.$endpoint+"/api/v1/service")
            .then((resp)=>{
                this.loader = false;
                var data = resp.data.data;
                console.log(data);
                for(var i of data){
                    if(i.name == this.service_name){
                        this.service_id = i._id;
                        break;
                    }
                }
                console.log('good');
            })
            .then((resp)=>{
                this.loader = false;
                axios.get(this.$endpoint+"/api/v1/sub-service/"+this.service_id)
                .then((resp)=>{
                    console.log(resp.data.data);
                    for(var i of resp.data.data){
                        if(i.children.length == 0){
                            this.sub_service.push({
                                "name" : i.name,
                                "_id" : i._id
                            });
                        }else if(i.children.length > 0){
                            for(var x of i.children){
                                this.sub_service.push({
                                    "name" : i.name,
                                    "_id" : i._id
                                });
                            }
                        }
                    }
                })
                .catch((err)=>{
                    this.loader = false;
                    alert(err.response.data.message);
                    console.log(err);
                })
            })
            .catch((err)=>{
                this.loader = false;
                alert(err.response.data.message);
                console.log(err.response);
            })
        },
 
        methods: {
            /**
            * When the location found
            * @param {Object} addressData Data of the found location
            * @param {Object} placeResultData PlaceResult object
            * @param {String} id Input container ID
            */
            getAddressData: function (addressData, placeResultData, id) {
                this.loader = true;
                this.address = addressData;
                var latitude = addressData.latitude;
                var longitude = addressData.longitude;
                axios.get(this.$endpoint+'/api/v1/partner',{
                    params:{
                        service:this.service_id,
                        latitude : latitude,
                        longitude : longitude
                    }
                })
                .then((resp)=>{
                    this.loader = false;
                    console.log(resp.data.data);
                    this.partners = [];
                    for(var i of resp.data.data){
                        this.partners.push({
                            "_id": i._id,
                            "phone": i.phone,
                            "email": i.email,
                            "institution_name": i.institution_name,
                            "institution_address":i.institution_address
                        })
                    }
                })
                .catch((err)=>{
                    this.loader = false;
                    console.log(err);
                })

            },
            requst_app(){
                var p_id = this.partner_id_opt;
                var ser_id = this.subservice_id_opt;
                console.log(p_id);
                console.log(ser_id);
                if(this.app_date != ''){
                    this.loader = true;
                axios.post(this.$endpoint+"/api/v1/appointment/"+p_id+'/'+ser_id,
                {
                    "description" : "want to do an eye test from the tester dev",
                    "scheduled_date" : this.app_date
                },{
                    headers:{
                        "x-access-token": this.token
                    }
                })
                .then((resp)=>{
                    this.loader = false;
                    alert("Appointment Created");
                })
                .catch((err)=>{
                    this.loader = false;
                    console.log(err);
                })

                }else{
                    alert('Please choose the date');
                }
            }
        }
    }
</script> 
<style scoped>
    .btn_p{
        color:#fff;
        border:none;
        background:#247ED0
    }
</style>