<template class="main">
    <div class="body_main">
        <div class="main_wrapper">
        <div class="container-fluid">
            <div class="row cnt_p">
                <loader-gif v-if="loader"></loader-gif>
                <div class="sono_img_p">
                    <div class="sono">
                        <div class="sono1 text-center">
                            <img src="../assets/img/sonocare.png">
                        </div>
                        <div class="sono2">
                            <img src="../assets/img/sonoall.png" width="100%">
                        </div>
                    </div>
                </div>
                <div class="sono_cnt_bg">
                <div class="sono_cnt">
                    <div class="sono_cnt_c text-center">
                        <p class="p_login">LOGIN</p>
                        <p class="p_txt">Welcome back! Please login to your account</p>
                        <template v-if="!f_pass">
                            <form v-on:submit.prevent="login">
                                <input type="email" name="email" class="inp_txt col-md-12" placeholder="Username" v-model="email">
                                <input type="password" name="pass" class="inp_txt col-md-12 mt-3" placeholder="Password" v-model="password"> 
                                <div class="form-group m-4 d-flex justify-content-between flex-wrap">
                                    <div class="form-check">
                                    <input class="form-check-input" type="checkbox" id="gridCheck">
                                    <label class="form-check-label" for="gridCheck">
                                        Remember me
                                    </label>
                                    </div>
                                    <span class="f_pass" v-on:click="f_pass_on()">Forget Password</span>
                                </div>
                                <div class="btn_pd d-flex justify-content-between flex-wrap">
                                    <button type="submit" name="sub_login" class="l_btn">Login</button>
                                    <router-link to="/register">
                                        <button class="s_btn">Sign up</button>
                                    </router-link>
                                </div>
                            </form>
                        </template>
                        <template v-else>
                            <form v-on:submit.prevent="ret_pass">
                                <input type="email" name="email" class="inp_txt col-md-12" placeholder="Username" v-model="email">
                                <div class="form-group m-4 d-flex justify-content-between flex-wrap">
                                
                                </div>
                                <div class="btn_pd d-flex justify-content-between flex-wrap">
                                    <button type="submit" name="sub_login" class="l_btn btn-block">Retrieve Password</button>
                                    <button class="btn-block" v-on:click="f_pass_off()"><!--<i class="fa fa-arrow-alt-circle-left fa-4x text-center"></i>-->&#8592 back</button>
                                </div>
                            </form>
                        </template>
                        <div>
                              <div class="alert alert-danger" v-if="err_message != ''">{{err_message}}</div>
                              <div class="alert alert-info" v-if="suc_message != '' ">{{suc_message}} <button class="btn btn-primary" v-on:click="gotoRec()">Click here to recovery password &#8594</button></div>
                         </div>
                    </div>
                </div>
                </div>
            </div>
        </div>
    
    </div>
    </div>
</template>

<script>
    const axios = require('axios');
    import { Datetime } from 'vue-datetime';
    import 'vue-datetime/dist/vue-datetime.css'
    export default{
        components: {
            datetime: Datetime
            },
        data(){
            
            return{
                email : '',
                password : '',
                err_message : '',
                suc_message : '',
                f_pass : false,
                loader: false,

            }
        },
        methods:{
            gotoRec(){
                this.$router.push('/retrieve');
            },
            f_pass_on(){
                this.f_pass = true
            },
            f_pass_off(){
                this.f_pass = false
            },

            ret_pass(){
                this.loader = true;
                axios.post("http://sonocareapp.eastus.cloudapp.azure.com/api/v1/forgot-password", {
                    email:this.email,
                })
                .then((resp)=>{
                    this.loader = false;
                    this.suc_message = 'Please check your email for pin and continue to recovery your password';
                })
                .catch((err)=>{
                    this.loader = false;
                    this.err_message = 'Error in retrieving'
                })
            },
            login(){
                
                this.err_message = '';
                if(this.email && this.password != ''){
                this.loader = true;
                axios.post("http://sonocareapp.eastus.cloudapp.azure.com/api/v1/login", {
                    email:this.email,
                    password:this.password
                })
                .then((resp)=>{
                    this.loader = false;
                    if(resp.status === 200 && 'token' in resp.data){
                        this.$session.start()
                        this.$session.set('jwt', resp.data.token)
                        this.$session.set('data', resp.data.data)
                        this.$session.set('account_type', resp.data.data.type)
                        this.$session.set('view', resp.data.data.type)
                        if(resp.data.data.profile_picture != undefined){
                            this.$session.set('image', resp.data.data.profile_picture)
                        }
                        if(resp.data.data.type == 'patient'){
                            this.$router.push('/patient/profile')
                        }else if(resp.data.data.type == 'admin'){
                            this.$router.push('/')
                        }else if(resp.data.data.type == 'partner'){
                            this.$router.push('/partner/profile')
                        }
                    }
                })
                .catch((err)=>{
                    this.loader = false;
                    console.log(err);
                    if(err.response.data.message){
                        this.err_message = err.response.data.message;
                    }
                    else{
                        this.err_message = err;
                    }
                });
            }else{
                    this.err_message = "Please complete the details";
                }
            }
        },
        mounted(){
            this.$session.destroy();
        }
        
        
    }
</script>

