<template>
    <div>
    
    <div class="wrapper">
             
            <app-sidebar></app-sidebar>
            <div class="main-panel">
                <!-- Navbar -->
                <app-nav></app-nav>
            <div class="content">
            
                <div class="container-fluid">
                <div class="d-flex flex-wrap justify-content-between head_loc">
                        <div class="col-md-6 p-0">
                        
                            <p class="m-0 dir_title">Appointment</p>
                            <p class="m-0 dir_point">
                                <span>Home -</span>
                                <span>Sonocare -</span>
                                <span>Admin -</span>
                                <span class="p_point">Appointment</span>
                            </p>
                        </div>
                        <div class="col-md-6 p-0 dash_sear">
                            <div class="input-group sear">
                                <div class="input-group-prepend">
                                    <span class="input-group-text" id="basic-addon1"><i class="fa fa-search"></i></span>
                                </div>
                                <input type="text" class="form-control" placeholder="Appointment" aria-label="Appointment" aria-describedby="basic-addon1">
                            </div>
                            
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-12">
                            <div class="card strpied-tabled-with-hover">
                                <div class="card-body table-full-width table-responsive">
                                    <table class="table table-hover table-striped">
                                        <thead class="h_head">
                                            <th>Client</th>
                                            <th>Partner</th>
                                            <th>Date, Time</th>
                                            <th>Datails</th>
                                            <th>Status</th>
                                        </thead>
                                        <tbody class="h_body">
                                            <tr v-for="appointment in appointments">
                                                <td>{{appointment.client}}</td>
                                                <td>{{appointment.doctor}}</td>
                                                <td>{{appointment.date}}</td>
                                                <td>{{appointment.service}}</td>
                                                <td>{{appointment.status}}</td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                            
                        </div>
                    </div>
                </div>
            </div>
            </div>
            <loader-gif v-if="loader"></loader-gif>
    </div>
    </div>
</template>


<script>
    const axios = require('axios');
    export default{
        data(){
            return {
                token : '',
                appointments : [],
                app_count : '',
                loader : false
            }
        },
        mounted(){
            this.loader = true;
            this.token = this.$session.get('jwt');
            axios.get(this.$endpoint+'/api/v1/appointment/admin',{
                headers:{
                    'x-access-token':this.token
                }
            })
            .then((resp)=>{
                this.loader = false;
                var data = resp.data.data.reverse();
                for(var i of data){
                    let dater = new Date(i.patient_scheduled_date);
                    dater = dater.toString();
                    console.log(dater);
                    var pos = dater.indexOf("G");
                    dater = dater.slice(0,pos);
                    this.appointments.push({
                        "_id":i._id,
                        "client":i.patient.first_name + " " + i.patient.last_name,
                        "doctor":i.partner.institution_name,
                        "date":dater,
                        "status":i.status,
                    })
                }
            })
            .catch((err)=>{
                this.loader = false;
                console.log(err);
                if(this.appointments == ""){
                    this.appointments.push({
                        "client":"EMPTY",
                        "doctor":"EMPTY",
                        "date":"EMPTY",
                        "status":"EMPTY",
                        "service":"EMPTY",
                    })
                }
            })
        }
    }

</script>

<style scoped>

       
        .h_body tr:hover{
            background:#006CF1!important;
            color:#fff!important;
        }
        .h_head th{
            font-weight:bold!important;
