<template>
    <div>
    <div class="wrapper">
            <pa-app-sidebar></pa-app-sidebar>
            <div class="main-panel">
                <!-- Navbar -->
                <pa-app-nav></pa-app-nav>
            <div class="content">
                <div class="container-fluid">
                <div class="d-flex flex-wrap justify-content-between head_loc">
                        <div class="col-md-6 p-0">
                            <p class="m-0 dir_title">History</p>
                            <p class="m-0 dir_point">
                                <span>Home -</span>
                                <span>Sonocare -</span>
                                <span>Partner -</span>
                                <span class="p_point">History</span>
                            </p>
                        </div>
                        <div class="col-md-6 p-0 dash_sear">
                            <div class="input-group sear">
                                <div class="input-group-prepend">
                                    <span class="input-group-text" id="basic-addon1"><i class="fa fa-search"></i></span>
                                </div>
                                <input type="text" class="form-control" placeholder="Appointment" aria-label="Appointment" aria-describedby="basic-addon1">
                            </div>
                            
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-12">
                            <div class="card striped-tabled-with-hover">
                                <div class="card-body table-responsive">
                                    <table class="table table-hover table-striped">
                                        <thead class="h_head">
                                            <th>Client</th>
                                            <th>Date, Time</th>
                                            <th>Datails</th>
                                            <th>Status</th>
                                            <th>Action</th>
                                        </thead>
                                        <tbody class="h_body">
                                            <tr v-for="appointment in appointments">
                                                <td>{{appointment.client}}</td>
                                                <td>{{appointment.date}}</td>
                                                <td>{{appointment.service}}</td>
                                                <td>{{appointment.status}}</td>
                                                <td><a><button class="btn btn-info" @click="appoint_detail(appointment._id)">View</button></a></td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                            
                        </div>
                    </div>
                </div>
            </div>
            </div>
            <loader-gif v-if="loader"></loader-gif>
    </div>
    </div>
</template>

<script>
    const axios = require('axios');
    export default{
        data(){
            return{
                token:'',
                appointments:[],
                loader : false,
            }
        },
        methods:{
            appoint_detail(_id){
                this.$router.push('/partner/appointdetail/'+_id);
            }
        },
        mounted(){
            this.loader = true;
            this.token = this.$session.get('jwt');
            axios.get(this.$endpoint+"/api/v1/appointment/partner",{
                headers:{
                    "x-access-token":this.token
                }
            })
            .then((resp)=>{
                this.loader = false;
                var data = resp.data.data.reverse();
                for(var i of data){
                    if(i.status == 'partner-accepted'){
                    let dater = new Date(i.patient_scheduled_date);
                    dater = dater.toString();
                    console.log(dater);
                    var pos = dater.indexOf("G");
                    dater = dater.slice(0,pos);
                    this.appointments.push({
                        "_id":i._id,
                        "client":i.patient.first_name + " " + i.patient.last_name,
                        "date":dater,
                        "status":i.status,
                        "service":i.patient.phone
                        // "service":i.service.name
                    })
                    //service is not found in the data so I replaced it patient detail
                }
            }
                if(this.appointments == ""){
                    this.appointments.push({
                        "client":"EMPTY",
                        "date":"EMPTY",
                        "status":"EMPTY",
                        "service":"EMPTY",
                    })
                }
                console.log("resp",this.appointments);

            })
            .catch((err)=>{
                this.loader = false;
                alert(err.response.data.message);
                console.log(err);
                if(this.appointments == ""){
                    this.appointments.push({
                        "client":"EMPTY",
                        "date":"EMPTY",
                        "status":"EMPTY",
                        "service":"EMPTY",
                    })
                }
            })
        }
    }

</script>
