<script>
    import VueGoogleAutocomplete from 'vue-google-autocomplete'
    const axios = require('axios');
    export default {
        components: { VueGoogleAutocomplete },
        data(){
            return{
                loader : false,
                err_message : '',
                tnc : false,
                mem : 'partner',
                address : '',
                patient:{
                    f_name : '',
                    l_name : '',
                    address : '',
                    phone : '',
                    email : '',
                    dob : '',
                    sex : 'male',
                    income_range : 'Income Range',
                    password : '',
                    password_c : '',
                },
                partner:{
                    institution_name : '',
                    address : '',
                    phone : '',
                    email : '',
                    institution_type : 'Institution Type',
                    password : '',
                    password_c : '',
                },
                'account_type':'Partner',
            }
        },
        methods:{
             /**
            * When the location found
            * @param {Object} addressData Data of the found location
            * @param {Object} placeResultData PlaceResult object
            * @param {String} id Input container ID
            */
            getAddressData: function (addressData, placeResultData, id) {
                this.loader = true;
                this.address = addressData;
                var latitude = addressData.latitude;
                var longitude = addressData.longitude;
                axios.get(this.$endpoint+'/api/v1/partner',{
                    params:{
                        service:this.service_id,
                        latitude : latitude,
                        longitude : longitude
                    }
                })
                .then((resp)=>{
                    this.loader = false;
                    this.partner.address = this.patient.address = this.address.route+" "+this.address.locality+" "+this.address.country;
                })
                .catch((err)=>{
                    this.loader = false;
                    console.log(err);
                })
            },
            
            checker(){
                // alert(this.tnc);
            },
            s_patient:function(){
                console.log('hey patient');
                this.account_type = 'Patient';
                this.mem = 'patient'
            },
            s_partner:function(){
                console.log('hey partner');
                this.account_type = 'Partner';
                this.mem = 'partner'
            },
            submit_patient(){
                
                this.err_message = '';
                var address = this.patient.address;
                if(address == ''){
                    address = this.address.route+" "+this.address.locality+" "+this.address.country;
                }
                 if(this.tnc == true){
                    if(this.patient.password == this.patient.password_c){
                        this.loader = true;
                    axios.post("http://sonocareapp.eastus.cloudapp.azure.com/api/v1/patient/signup",{
                        "first_name" : this.patient.first_name,
                        "last_name" :  this.patient.last_name,
                        "password" : this.patient.password,
                        "date_of_birth" : this.patient.dob,
                        "phone" : this.patient.phone,
                        "sex" : this.patient.sex,
                        "email" : this.patient.email,
                        "address" : address
                    })
                    .then((resp)=>{
                        this.loader = false;
                        this.err_message = "patient successfully register";
                        if(resp.status == 200){
                            this.$session.start()
                            this.$session.set('jwt', resp.data.token)
                            this.$session.set('account_type', resp.data.data.type)
                            this.$session.set('data', resp.data.data)
                            this.$router.push('/patient/profile');
                        }
                    }).catch((err)=>{
                        this.loader = false;
                        console.log(err.response.message);
                        console.log(err.data.message);
                        if(err.response.data.message){
                            this.err_message = err.response.data.message;
                        }else{
                            this.err_message = err;
                        }
                    });
                }else{
                    this.err_message = 'password not match';
                }
              }else{
                  this.err_message = 'Please you must agree with terms and conditions';
              }
            },
            submit_partner(){
                this.err_message = '';
                var address = this.partner.address;
                if(address == ''){
                    address = this.address.route+" "+this.address.locality+" "+this.address.country;
                }   
                console.log(this.partner.address);
                console.log(this.address);
                if(this.tnc == true){
                if(this.partner.password == this.partner.password_c){
                    this.loader = true;
                    axios.post("http://sonocareapp.eastus.cloudapp.azure.com/api/v1/partner/signup", {
                        "institution_type" : this.partner.institution_type,
                        "institution_name" : this.partner.institution_name,
                        "institution_address" : address,
                        "password" : this.partner.password,
                        "phone" : this.partner.phone,
                        "email" : this.partner.email
                    }).then((resp)=>{
                        this.loader = false;
                        this.err_message = "partner successfully register";
                        if(resp.status == 200){
                            this.$session.start()
                            this.$session.set('jwt', resp.data.token)
                            this.$session.set('account_type', resp.data.data.type)
                            this.$session.set('data', resp.data.data)
                            this.$router.push('/partner/profile');
                        }
                    }).catch((err)=>{
                        this.loader = false;
                        console.log(err.response);
                        if(err.response.data.message){
                            this.err_message = err.response.data.message;
                        }else{
                            this.err_message = err;
                        }
                    });
                }else{
                    this.err_message = 'password not match';
                }
            }else{
                   this.err_message = 'Please you must agree with terms and conditions';
                }
            }
        },
        mounted(){
            // console.log(this.$session.get('data'));
        }
    }
</script>

