<template>
    <div>
    
        <div class="wrapper">
            <app-sidebar></app-sidebar>
            <div class="main-panel">
                <!-- fixed plugin  -->
                <!-- Navbar -->
                <app-nav></app-nav>
        
                <!-- End Navbar -->
            <div class="content">
                <div class="container-fluid">
                    <div class="d-flex flex-wrap justify-content-between head_loc">
                        <div class="col-md-6 p-0">
                            <p class="m-0 dir_title">Services</p>
                            <p class="m-0 dir_point">
                                <span>Home -</span>
                                <span>Sonocare -</span>
                                <span class="p_point">Services</span>
                            </p>
                        </div>
                        <div class="col-md-6 p-0 dash_sear">
                                <div class="input-group sear">
                                    <div class="input-group-prepend">
                                        <span class="input-group-text" id="basic-addon1"><i class="fa fa-search"></i></span>
                                    </div>
                                    <input type="text" class="form-control" placeholder="Service" aria-label="Service" v-model="searchService">
                                </div>
                                
                        </div>
                    </div>
                    <div class="row">
                        <div class="container-fluid">
                            <div class="add_service">
                                <button class="service_btn" @click="addNewServiceForm()">Add New Service</button>
                            </div>
                            <div class="new_service_detail" v-if="add_new_service">
                                <div class="new_service mt-3">
                                    <div class="col-md-6 p-0">
                                        <input type="text" class="form-control" v-model="new_service_name" placeholder="Service Name"><br>
                                        <textarea type="text" class="form-control" v-model="new_service_desc" placeholder="Service Description"></textarea><br>
                                        <button class="btn-block add_service_btn" @click="addNewService()">Add Service</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="row">
                        <div class="container-fluid service_p">
                            <div class="row">
                                <div class="col-md-4 service_gp" v-for="(service,index) in filterService">
                                    <div class="service_c text-center">

                                            <img v-bind:src="service.picture" class="rounded-circle p_img" width="100px"><br>
                                        <div class="form-group">
                                             <label v-bind:for="'image_edit'+index"><i class="fa fa-edit"></i>Edit</label>
                                             <input type="file" ref="file" class="form-control-file" v-bind:id="'image_edit'+index" style="display:none" v-on:change="handleFileUpload(service,$event)">
                                        </div> 
                                        <template v-if="!service.edit_service">
                                            <span>{{service.name}}</span><br>
                                            <button class="edit_service" @click="editService(service)">Edit</button>
                                        <button class="cancel_edit_service" @click="deleteService(index,service)">Delete Service</button>
                                        <button class="sub_service" @click="goto_sub_service(service)">Sub Service</button>
                                        </template>

                                        <template v-else>
                                            <br>
                                            <input type="text" class="form-control" v-model="service.name">
                                            <textarea type="text" class="form-control" v-model="service.description"></textarea>
                                            <button class="edit_service" @click="saveEditService(service)">Save</button>
                                            <button class="cancel_edit_service" @click="cancelEditService(service)">cancel</button>
                                        </template>
                                    </div>
                                </div>
                                
                            </div>
                        </div>
                    </div>
                </div>
            </div>
    </div>
            <loader-gif v-if="loader"></loader-gif>
    </div>
    </div>
</template>

<script>
    const axios = require('axios');
   export default {
  name: 'panel',
  data () {
    return { 
        file : '',
        token:'',
        searchService:'',
        add_new_service:false,
        new_service_name:'',
        new_service_desc:'',
        services:[],
        serviced:[
            {
                image:'',
                name: 'UV Scan',
                description:'Scanning using Ultra violet ray',
                edit_service : false,
                temp_name : 'UV Scan',
                temp_desc : 'Scanning using Ultra violet ray',
            },
            {
                image:'',
                name: 'ECG Scan',
                description:'Scanning using ECG scanning machine',
                edit_service : false,
                temp_name : 'ECG Scan',
                temp_desc : 'Scanning using ECG scanning machine',
            },
            {
                image:'',
                name: 'ENDO Scan',
                description:'Scanning using endo scanning machine',
                edit_service : false,
                temp_name : 'ENDO Scan',
                temp_desc : 'Scanning using Ultra violet ray',
            },
            {
                image:'',
                name: 'Lab Test',
                description:'Taking test in the lab',
                edit_service : false,
                temp_name : 'Lab Test',
                temp_desc : 'Taking test in the lab',
            },
            {
                image:'',
                name: 'Consultation',
                description:'We offer consultance service ',
                edit_service : false,
                temp_name : 'Consultation',
                temp_desc : 'We offer consultance service',
            },
        ],
        loader : false,
    }
  },
  computed:{
            filterService(){
                return this.services.filter((service)=>{
                    for(i in 10){
                        console.log('dog');
                    }
                    return service.name.toLowerCase().includes(this.searchService.toLowerCase())
                })
            },
            
        },
  methods: {
      handleFileUpload(service,event){
               var _id = service._id;
                this.file = event.target.files[0];
                console.log(this.file);

                let formData = new FormData();

                    formData.append('image', this.file);
                    this.loader = true;
                    axios.post('http://sonocareapp.eastus.cloudapp.azure.com/api/v1/service/image/'+_id,
                        formData,
                        {
                        headers: {
                            'Content-Type': 'multipart/form-data',
                            'x-access-token' : this.token
                        }
                        }
                    ).then((resp)=>{
                        this.loader = false;
                        console.log(resp);
                        // this.$session.set('image',resp.data.data.picture);
                        service.picture = resp.data.data.picture
                })
                .catch((err)=>{
                    this.loader = false;
                    console.log(err);
                });
                console.log(this.token);
                        console.log(service.picture);
      },
      editService(service){
          service.edit_service = true;
      },
      cancelEditService(service){
          service.edit_service = false;
          service.name = service.temp_name;
          service.description = service.temp_desc;
      },
      addNewServiceForm(){
          this.add_new_service = !this.add_new_service;
      },
      saveEditService(service){
          this.loader = true;
          var _id = service._id;
          axios.put("http://sonocareapp.eastus.cloudapp.azure.com/api/v1/service/"+_id,
            {
                "name" : service.name,
                "description" : service.description,
            },
            {
                headers:{
                    "x-access-token":this.token
                }
            })
            .then((resp)=>{
                this.loader = false;
                service.edit_service = false;
                console.log(resp);
            })
            .catch((err)=>{
                this.loader = false;
                console.log(err.response);
            })
      },
      addNewService(){
          if(this.new_service_name != '' && this.new_service_desc != ''){
              this.loader = true;
                axios.post("http://sonocareapp.eastus.cloudapp.azure.com/api/v1/service",
                    {  
                        "name" : this.new_service_name,
                        "description" : this.new_service_desc,
                    },
                    {
                        headers:{
                            "x-access-token":this.token
                        }
                    }
                )
                .then((resp)=>{
                    this.loader = false;
                    this.services.unshift({
                        _id : resp.data.data._id,
                        picture:'https://cdn1.iconfinder.com/data/icons/inficons-set-3-1/1000/chemistry-flasks-2-512.png',
                        name: this.new_service_name,
                        description: this.new_service_desc,
                        edit_service : false,
                        temp_name : this.new_service_name,
                        temp_desc : this.new_service_desc,
                    });
                    this.addNewServiceForm();
                    console.log('resp',resp);
                })
                .catch((err)=>{
                    this.loader = false;
                     console.log(err)
                })
            }
      },
      
      deleteService(index,service){
          
          var _id = service._id;
          var ret = confirm("Are you sure");
          if(ret == true){
                this.loader = true;
                axios.delete("http://sonocareapp.eastus.cloudapp.azure.com/api/v1/service/"+_id,{
                headers:{
                    "x-access-token":this.token
                }
                })
                .then((resp)=>{
                    this.loader = false;
                    console.log('deleted');
                    this.services.splice(index,1);
                })
                .catch((err)=>{
                    this.loader = false;
                    console.log(err.response);
                })
          }
          


      },
      goto_sub_service(service){
            var _id = service._id;
            this.$router.push('/subservice/'+_id);
      },
      logout: function () {
        this.$session.destroy()
        this.$router.push('/login')
        }
    
    },
    mounted(){
        this.loader = true;
        this.token = this.$session.get('jwt');
        axios.get(this.$endpoint+"/api/v1/service")
        .then((resp)=>{
            this.loader = false;
            for(var i of resp.data.data){
                if(i.picture == null){
                    i.picture = 'https://cdn1.iconfinder.com/data/icons/inficons-set-3-1/1000/chemistry-flasks-2-512.png';
                }
                this.services.push({
                    "_id": i._id,
                    "picture":i.picture,
                    "name":i.name,
                    "description":i.description,
                    "edit_service" : false,
                    "temp_name" : i.name,
                    "temp_desc" : i.description,
                });
                }
                console.log('resp',resp);
        })
        .catch((err)=>{
            this.loader = false;
            console.log(err)
        })
  }
}
</script>

