<template>
    <div>
        <nav class="navbar navbar-expand-lg " color-on-scroll="500">
                <div class="container-fluid d-flex justify-content-end">
                    <button v-on:click="toggler" href="#" class="navbar-toggler navbar-toggler-right" type="button" data-toggle="collapse" aria-controls="navigation-index" aria-expanded="false" aria-label="Toggle navigation">
                        <span class="navbar-toggler-bar burger-lines"></span>
                        <span class="navbar-toggler-bar burger-lines"></span>
                        <span class="navbar-toggler-bar burger-lines"></span>
                    </button>
                    <div class="collapse navbar-collapse justify-content-end" id="navigation">
                        <ul class="navbar-nav ml-auto">
                            <li class="nav-item dropdown">
                                <a class="nav-link dropdown-toggle" id="navbarDropdownMenuLink" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                    <img v-bind:src="profile_img" width="30px" class="rounded-circle d-lg-none">
                                    <span class="no-icon">Admin</span>
                                </a>
                                <div class="dropdown-menu" aria-labelledby="navbarDropdownMenuLink">
                                    <router-link to="/login"><a class="dropdown-item">Logout</a></router-link>
                                </div>
                            </li>
                            <li class="nav-item d-none d-lg-block">
                                <a class="nav-link">
                                    <img v-bind:src="profile_img"  width="30px" class="rounded-circle">
                                </a>
                            </li>
                        </ul>
                    </div>
                </div>
            </nav>
    </div>
</template>

<script>

    export default{
        data(){
            return{
                profile_img : '',
            }
        },
        beforeCreate: function () {
            // alert(this.$session.get('account_type'))
            
            if (!this.$session.exists()) {
              this.$router.push('/login')
            }else{
                if(this.$session.get('account_type') != 'admin'){
                    this.$router.push('/login');
                }
            }
        },
        methods: {
            logout: function () {
            this.$session.destroy()
            this.$router.push('/login')
            },
            toggler: function (){
                $('html').toggleClass('nav-open');
            }
        },
        mounted(){

            if($('html').hasClass('nav-open')){
                $('html').toggleClass('nav-open');
            }
            if(this.$session.get('image')){
                this.profile_img = this.$session.get('image');
            }else{
                this.profile_img = 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcR05-WvgvoVVFR0fQCTWhyhiTHGinaX4FK8-Dux8QrLzrSZ8oQ3SQ';
            }
        }
    }

</script>

