<template>
    <div>
    
        <div class="wrapper">
            <app-sidebar></app-sidebar>
            <div class="main-panel">
                <!-- fixed plugin  -->
                <!-- Navbar -->
                <app-nav></app-nav>
        
                <!-- End Navbar -->
            <div class="content">
                <div class="container-fluid">
                    <div class="d-flex flex-wrap justify-content-between head_loc">
                        <div class="col-md-6 p-0">
                            <p class="m-0 dir_title">Services</p>
                            <p class="m-0 dir_point">
                                <span>Home -</span>
                                <span> Sonocare -</span>
                                <span class="p_point"> Services -</span>
                                <span class="p_point"> Sub-services</span>
                            </p>
                        </div>
                        <div class="col-md-6 p-0 dash_sear">
                                <div class="input-group sear">
                                    <div class="input-group-prepend">
                                        <span class="input-group-text" id="basic-addon1"><i class="fa fa-search"></i></span>
                                    </div>
                                    <input type="text" class="form-control" placeholder="Service" aria-label="Service" v-model="search_subservice">
                                </div>
                        </div>
                    </div>
                    <div class="row">
                        <div class="container-fluid">
                            <div class="add_service">
                                <button class="service_btn" @click="addNewSubserviceForm()">Add New Sub Service</button>
                            </div>
                            <div class="new_service_detail" v-if="add_new_subservice">
                                <div class="new_service mt-3">
                                    <div class="col-md-6 p-0">
                                        <input type="text" class="form-control" v-model="new_subservice_name" placeholder="SubService Name"><br>
                                        <textarea type="text" class="form-control" v-model="new_subservice_desc" placeholder="SubService Description"></textarea><br>
                                        <input type="number" class="form-control" v-model="new_subservice_amount" placeholder="SubService Amount"><br>
                                        <div class="form-group ml-1">
                                    <div class="col-md-12 d-flex justify-content-between flex-wrap">
                                        <input class="form-check-input" type="checkbox" id="gridCheck" v-model:checked="new_subservice_homeService">
                                        <label class="form-check-label" for="gridCheck">
                                            Home Service
                                        </label>
                                    </div>
                                </div>
                                        <button class="btn-block add_service_btn" @click="addNewSubservice()">Add Sub Service</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="row">
                        <div class="container-fluid service_p">
                            <div class="row">
                                <div class="col-md-4 service_gp" v-for="(subservice,index) in filterService">
                                    <div class="service_c text-center">

                                            <img v-bind:src="subservice.picture" class="rounded-circle p_img" width="100px"><br>
                                        <div class="form-group">
                                             <label v-bind:for="'image_edit'+index"><i class="fa fa-edit"></i>Edit</label>
                                             <input type="file" ref="file" class="form-control-file" v-bind:id="'image_edit'+index" style="display:none" v-on:change="handleFileUpload(subservice,$event)">
                                        </div> 
                                        <template v-if="!subservice.edit_service">
                                            <span>{{subservice.name}}</span><br>
                                            <button class="edit_service" @click="editSubservice(subservice)">Edit</button>
                                        <button class="cancel_edit_service" @click="deleteSubservice(index,subservice)">Delete Subservice</button>
                                        <button class="child-service" @click="child(subservice)">View Child</button>
                                        </template>

                                        <template v-else>
                                            <br>
                                            <input type="text" class="form-control" v-model="subservice.name">
                                            <textarea type="text" class="form-control" v-model="subservice.description"></textarea>
                                            <input type="text" class="form-control" v-model="subservice.amount">
                                            <div class="col-md-12 d-flex justify-content-between flex-wrap">
                                                    <input class="form-check-input" type="checkbox" id="gridCheck" v-model:checked="subservice.homeService">
                                                <label class="form-check-label" for="gridCheck">
                                                    Home Service
                                                </label>
                                             </div>
                                            <button class="edit_service" @click="saveEditSubservice(subservice)">Save</button>
                                            <button class="cancel_edit_service" @click="cancelEditSubservice(subservice)">cancel</button>
                                        </template>
                                    </div>
                                </div>
                                
                            </div>
                        </div>
                    </div>
                </div>
            </div>
    </div>
             <loader-gif v-if="loader"></loader-gif> 
    </div>
    </div>
</template>

<script>
    const axios = require('axios');
    const qs = require('query-string');
   export default {
  name: 'panel',
  data () {
    return { 
        file : '',
        service_id:this.$route.params.service,
        subservices:[],
        token:'',
        search_subservice:'',
        add_new_subservice:false,
        new_subservice_name:'',
        new_subservice_desc:'',
        new_subservice_amount:0,
        new_subservice_homeService:false,
        subservices:[],
        loader : false,
        
    }
  },
  computed:{
            filterService(){
                return this.subservices.filter((subservice)=>{
                    return subservice.name.toLowerCase().includes(this.search_subservice.toLowerCase())
                })
            },
            
},
    methods: {
        handleFileUpload(subservice,event){
               var _id = subservice._id;
               console.log(_id);
                this.file = event.target.files[0];
                console.log(this.file);

                let formData = new FormData();
                    this.loader = true;
                    formData.append('image', this.file);

                    axios.post(this.$endpoint+'/api/v1/sub-service/image/'+_id,
                        formData,
                        {
                        headers: {
                            'Content-Type': 'multipart/form-data',
                            'x-access-token' : this.token
                        }
                        }
                    ).then((resp)=>{
                        this.loader = false;
                        console.log(resp);
                        // this.$session.set('image',resp.data.data.picture);
                        subservice.picture = resp.data.data.picture
                })
                .catch((err)=>{
                    this.loader = false;
                    console.log(err);
                });
                console.log(this.token);
                        console.log(subservice.picture);
      },
        child(subservice){
            var _id = subservice._id;
            this.$router.push('/subchild/'+_id);
        },
      editSubservice(subservice){
          subservice.edit_service = true;
      },
      cancelEditSubservice(subservice){
          subservice.edit_service = false;
          subservice.name = subservice.temp_name;
          subservice.description = subservice.temp_desc;
          subservice.amount = subservice.temp_amount;
      },
      addNewSubserviceForm(){
          this.add_new_subservice = !this.add_new_subservice;
      },
      saveEditSubservice(subservice){
          this.loader = true;
          var _id = subservice._id;
          console.log(subservice.homeService);
          axios.put(this.$endpoint+"/api/v1/sub-service/"+_id,
            {
                "name" : subservice.name,
                "description" : subservice.description,
                "amount" : subservice.amount,
                "homeService" : subservice.homeService
            },
            {
                headers:{
                    "x-access-token":this.token
                }
            })
            .then((resp)=>{
                this.loader = false;
                subservice.edit_service = false;
                console.log(resp);
            })
            .catch((err)=>{
                this.loader = false;
                console.log(err.response);
            })
      },
      addNewSubservice(){
          var _id = this.service_id;
          console.log(this.new_subservice_homeService);
          const data = qs.stringify({  
                        "name" : this.new_subservice_name,
                        "description" : this.new_subservice_desc,
                        "amount" : this.new_subservice_amount,
                        "home_service" : this.new_subservice_homeService
                    });
          if(this.new_subservice_name != '' && this.new_subservice_desc != ''){
              this.loader = true;
                axios.post(this.$endpoint+"/api/v1/sub-service/"+_id,
                    data
                    ,
                    {
                        headers:{
                            "Content-Type":"application/x-www-form-urlencoded",
                            "x-access-token":this.token
                        }
                    }
                )
                .then((resp)=>{
                    this.loader = false;
                    this.subservices.unshift({
                        _id : resp.data.data._id,
                        picture:'https://cdn1.iconfinder.com/data/icons/inficons-set-3-1/1000/chemistry-flasks-2-512.png',
                        name: this.new_subservice_name,
                        description: this.new_subservice_desc,
                        amount : this.new_subservice_amount,
                        homeService: this.new_subservice_homeService,
                        edit_service : false,
                        temp_name : this.new_subservice_name,
                        temp_desc : this.new_subservice_desc,
                        temp_amount : this.new_subservice_amount,
                    });
                    this.addNewSubserviceForm();
                    console.log('resp',resp);
                })
                .catch((err)=>{
                    this.loader = false;
                    console.log(err)
                })
            }
      },
      
      deleteSubservice(index,subservice){
          var _id = subservice._id;
        //   console.log(_id);
            var ret = confirm('Are you sure');
            if(ret == true){   
                this.loader = true; 
                axios.delete(this.$endpoint+"/api/v1/sub-service/"+_id,{
                    headers:{
                        "Content-Type": "application/text/plain",
                        "x-access-token":this.token
                    }
                })
                .then((resp)=>{
                    this.loader = false;
                    this.subservices.splice(index,1);
                })
                .catch((err)=>{
                    this.loader = false;
                    console.log(err.response);
                })
            }

      },

      logout: function () {
        this.$session.destroy()
        this.$router.push('/login')
        }
    
    },
    mounted(){
        // alert(this.service_id);
        this.loader = true;
        this.token = this.$session.get('jwt');
        console.log(this.token);
        axios.get(this.$endpoint+"/api/v1/sub-service/"+this.service_id)
        .then((resp)=>{
            this.loader = false;
            for(var i of resp.data.data){
                if(i.picture == null){
                    i.picture = 'https://cdn1.iconfinder.com/data/icons/inficons-set-3-1/1000/chemistry-flasks-2-512.png';
                }
                this.subservices.push({
                    "_id": i._id,
                    "picture":i.picture,
                    "name":i.name,
                    "description":i.description,
                    "amount":i.amount,
                    "homeService":i.homeService,
                    "edit_service" : false,
                    "temp_name" : i.name,
                    "temp_desc" : i.description,
                    "temp_amount" : i.amount,
                });
                }
                console.log('resp',resp.data.data);
        })
        .catch((err)=>{
            this.loader = false;
            console.log(err.response)
        })
  }
}
</script>

