                                <span>Sonocare -</span>
                                <span>Patient -</span>
                                <span class="p_point">History</span>
                            </p>
                        </div>
                        <div class="col-md-6 p-0 dash_sear">
                            <div class="input-group sear">
                                <div class="input-group-prepend">
                                    <span class="input-group-text" id="basic-addon1"><i class="fa fa-search"></i></span>
                                </div>
                                <input type="text" class="form-control" placeholder="History" aria-label="Appointment" aria-describedby="basic-addon1">
                            </div>
                            
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-12">
                            <div class="card strpied-tabled-with-hover">
                                <div class="card-body table-full-width table-responsive">
                                    <table class="table table-hover table-striped">
                                        <thead class="h_head">
                                            <th>Service Provider</th>
                                            <th>Date, Time</th>
                                            <th>Datails</th>
                                            <th>Status</th>
                                            <th>Action</th>
                                        </thead>
                                        <tbody class="h_body">
                                            <tr v-for="appointment in appointments">
                                                <td>{{appointment.doctor}}</td>
                                                <td>{{appointment.date}}</td>
                                                <td>{{appointment.service}}</td>
                                                <td>{{appointment.status}}</td>
                                                 <td><a><button class="btn btn-info" @click="appoint_detail(appointment._id)">View</button></a></td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                            <div class="d-flex">
                                <nav aria-label="Page navigation example">
                                    <ul class="pagination">
                                        <li class="page-item">
