</template>

<script>
    const axios = require('axios');
    export default{
        data(){
            return{
                token:'',
                appointments:[],
                loader : false,
            }
        },
        methods:{
            appoint_detail(_id){
                this.$router.push('/patient/appointdetail/'+_id);
            }
        },  
        mounted(){
            this.loader = true,
            this.token = this.$session.get('jwt');
            axios.get(this.$endpoint+"/api/v1/appointment/patient",{
                headers:{
                    "x-access-token":this.token
                }
            })
            .then((resp)=>{
                this.loader = false;
                console.log(resp);
                var data = resp.data.data;
                
                for(var i of data){
                    if(i.status == 'partner-accepted'){
                    let dater = i.patient_scheduled_date;
                    console.log(dater);
                    var pos = dater.indexOf(".");
                    dater = dater.slice(0,pos);
                    dater = dater.replace('T',' ');
                    this.appointments.push({
                        "_id":i._id,
                        "doctor":i.partner.institution_name,
                        "date":dater,
                        "status":i.status,
                        "service":i.partner.phone
                        // "service":i.service.name
                    });
                    }
                }
                console.log("resp",this.appointments);
                this.loader = true;
            }).then((resp)=>{
                this.loader = false;
                if(this.appointments == null){
                    this.appointments.push({
                        "doctor":'EMPTY',
                        "date":'EMPTY',
                        "status":'EMPTY',
                        "service":'EMPTY',
                    })
                }
            })
            .catch((err)=>{
                this.loader = false;
                console.log(err);
                if(this.appointments == ""){
                    this.appointments.push({
                        "doctor":"EMPTY",
                        "date":"EMPTY",
                        "status":"EMPTY",
                        "service":"EMPTY",
                    })
                }
            })
        }
    }

</script>
